body {
  margin: 0;
  padding: 0;
  color: #ececec;
  min-width: 320px;
  background-color: #232224;
  font-size: 18px;
  text-align: center; }

html {
  box-sizing: border-box;
  overflow-y: scroll;
  /* margin-left: calc(100vw - 100%);
  margin-right: 0; */ }

*,
*::before,
*::after {
  box-sizing: inherit; }

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  padding: 0; }

p {
  margin: 0;
  padding: 0; }

ul {
  padding: 0;
  margin: 0; }

li {
  list-style: none; }

.container {
  max-width: 480px;
  margin: 0 auto; }

.header {
  width: 100%;
  height: 70px;
  background-color: #09345a; }

.main {
  padding: 70px 0; }

.choise__link {
  display: block;
  margin-bottom: 50px; }
  .choise__link img {
    display: block;
    max-width: 100%;
    margin: 0 auto; }

.survivor__list {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between; }
  .survivor__list li {
    max-width: 100px;
    height: 150px;
    margin: 0 auto; }
    .survivor__list li img {
      max-width: 100%; }
    .survivor__list li p {
      text-align: center; }

.description {
  margin-bottom: 20px; }
  .description p {
    text-align: left; }

.table {
  width: 100%;
  margin-bottom: 20px;
  border: 1px solid #dddddd;
  border-collapse: collapse; }

.table th {
  font-weight: bold;
  padding: 5px;
  border: 1px solid #dddddd; }

.table td {
  border: 1px solid #dddddd;
  padding: 5px;
  text-align: center; }

.th-one {
  color: #e8c252; }

.th-two {
  color: mediumseagreen; }

.th-three {
  color: #912cee; }

.feature {
  margin-bottom: 20px; }
  .feature li {
    margin-bottom: 10px;
    text-align: left; }

.note {
  margin-bottom: 20px; }
  .note li {
    margin-bottom: 10px;
    text-align: left; }
